body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  display: grid;
  background-color: #2d3436;
  grid-template-rows: 500px 200px 275px;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
}

img {
  height: 300px;
  width: 225px;
}

#root {
  grid-row: 1 / 4;
}

.cardContainer {
  display: grid;
  justify-content: center;
  background-color: #636e72;
}

.cardContainer:hover {
  cursor: pointer;
}

.name {
  display: flex;
  margin: 0 auto;
}

.appContainer {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
}

.score {
  display: flex;
  justify-content: space-between;
}
.title {
    font-size: 7em;
    margin-top: 0;
    margin-bottom: 0
}

.underTitle {
    margin-top: 0;
    font-size: 2em;
}

.divTitle > * {
    color: white;
}
